.header {
  background-color: #dcbe87;
}

.header ul {
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.header li {
  display: inline-block;
  margin: 15px 10px 15px 10px;
}

.heading-nav {
  padding-left: 10px;
}

.login-nav {
  padding-right: 10px;
}

.home-link {
  text-decoration: none;
}

.search-nav input {
  margin-right: 10px;
  padding: 5px 10px;
  font-size: 0.8rem;
}






@media only screen and (max-width: 585px) {
    .search-nav div{
        display: none;
    }
}



