.signup {
  padding: 2em;
  padding-left: 10%;
  padding-right: 10%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.signup div {
  padding-top: 20px;
}

.table-signup {
  padding-top: 20px;
  width: 50%;
}

.table-signup td {
  padding-top: 5px;
  padding-bottom: 5px;
}

.table-input input{
  width: 100%;
  padding-top: 5px;
  padding-bottom: 5px;
}

.signup-errors {
  color: red;
}




@media only screen and (max-width: 800px) {
    .table-signup {
      width: 100%;
    }

    .signup {
      margin: 3% 15% 0 15%;
    }
}



